
const CompanyInfo = ({ companyInfo }) => {
    return (
        <>
            <h3 className="mb-1">{companyInfo.name}</h3>
            <div>Address: {companyInfo.address}</div>
            <div className="mt-2"><strong>Specialist in:</strong> {companyInfo.specialty}</div>
            <div className="mt-2"><strong>GST Number:</strong> {companyInfo.gstNumber}</div>
            <div>Marketing Manager: {companyInfo.manager}</div>
            <div>Email: {companyInfo.email}</div>
            <div>
                Website:{" "}
                <a target="_blank" rel="noopener noreferrer" href={`https://${companyInfo.website}`}>
                    {companyInfo.website}
                </a>
            </div>
            <div>Phone: {companyInfo.phone}</div>
        </>
    );
};

export default CompanyInfo;